import {SettingGroup, Setting} from '../Settings'
import { useApp } from "../../AppProvider"
import { useState, useEffect } from 'react';

export default function Notifications () {
  const {notifications, freeze} = useApp ();
  const [ampm, setAmpm] = useState ();
  const [time, setTime] = useState ();
  const createNotification = async () => {
    let unfreeze = freeze ();
    try {
      await notifications.createNotification ({notificationTime: `${time} ${ampm}`});
      unfreeze ();
    } catch (e) {
      unfreeze ();
      console.log (e);
    }
  }
  return (
    <SettingGroup longTitle="Manage Your Notifications" shortTitle="Notifications">
      {
        notifications.notifications.length > 0 &&
        <p>All you have to do is specifiy times you want to recieve reminders to enter in your journal and we will automatically send a text to you at that time each day!</p>
      }
      {
        notifications.notifications.map (note => (
          <Setting setting={`notification-${note.notificationTime}`} title={note.notificationTime}></Setting>
        ))
      }
      {
        notifications.notifications.length === 0 &&
        <p>You haven't created any notifications yet, and that's totally ok! Just remember we're always here if you need us!</p>
      }
      <div className="notification-creator">
        <select onChange={e => setTime (e.target.value)}>
          <option>12:00</option>
          <option>12:15</option>
          <option>12:30</option>
          <option>12:45</option>
          <option>1:00</option>
          <option>1:15</option>
          <option>1:30</option>
          <option>1:45</option>
          <option>2:00</option>
          <option>2:15</option>
          <option>2:30</option>
          <option>2:45</option>
          <option>3:00</option>
          <option>3:15</option>
          <option>3:30</option>
          <option>3:45</option>
          <option>4:00</option>
          <option>4:15</option>
          <option>4:30</option>
          <option>4:45</option>
          <option>5:00</option>
          <option>5:15</option>
          <option>5:30</option>
          <option>5:45</option>
          <option>6:00</option>
          <option>6:15</option>
          <option>6:30</option>
          <option>6:45</option>
          <option>7:00</option>
          <option>7:15</option>
          <option>7:30</option>
          <option>7:45</option>
          <option>8:00</option>
          <option>8:15</option>
          <option>8:30</option>
          <option>8:45</option>
          <option>9:00</option>
          <option>9:15</option>
          <option>9:30</option>
          <option>9:45</option>
          <option>10:00</option>
          <option>10:15</option>
          <option>10:30</option>
          <option>10:45</option>
          <option>11:00</option>
          <option>11:15</option>
          <option>11:30</option>
          <option>11:45</option>
        </select>
        <select onChange={e => setAmpm (e.target.value)}>
          <option>am</option>
          <option>pm</option>
        </select>
        <button disabled={!time || !ampm} onClick={createNotification}>Create Notification</button>
      </div>
    </SettingGroup>
  )
}