import {useEffect, useRef, useState} from 'react';
import {H1, H2, H3} from './components/Headers';
import AudioRecorder from './components/AudioRecorder';
import {audios as audioApi} from '../lib/auth';
import { useApp } from "../AppProvider";
import {printDate, getTime} from '../lib/indexing';

let defaultQuestions = [
  'What have I done today?',
  'How do I feel?',
  'What else am I going to do?'
];

function CustomQuestionSetting ({question, onToggle, getSetting}) {
  return (
    <li className="setting">
      <div>{question}</div>
      <div><span onClick={onToggle (`show-custom-question-${question}`)} className={getSetting (`show-custom-question-${question}`) ? 'toggle on' : 'toggle'} /></div>
    </li>
  )
}

export default function Write () {
  // state mgmt
  const {auth: {user}, router: {page}, journal: {createEntry}, settings: {getSetting, toggle, set}, writing: {questions, createQuestion, recordingStatus, resetAudioInformation}, audio: {audioRecording, audioRecordingTitle}, session, freeze} = useApp ();
  const [start, setStart] = useState (new Date ());
  const [focusList, setFocusList] = useState ({});
  const newQuestionRef = useRef ();
  const freeformRef = useRef ();
  
  // clear content on first focus
  const onFirstFocus = (key, text='') => e => {
    if (focusList [key]) return;
    e.target.innerText = text;
    setFocusList ({
      ...focusList,
      [key]: true
    })
  }

  // call api to create freeform entry
  const runSubmitFreeformEntry = async e => {
    let unfreeze = freeze ();
    try {
      const entry = {
        entryType: 'freeform',
        freeform: freeformRef.current.innerText,
        start,
        end: new Date ()
      }
      await createEntry (entry);
      freeformRef.current.innerText = 'Your entry here!';
      setStart (new Date ());
      unfreeze ();
    } catch (error) {
      unfreeze ();
    }
  }

  // call api to create qa entry
  const runSubmitQuestionAnswerEntry = async e => {
    let unfreeze = freeze ();
    try {
      let qa = session.getAll ();
      const entry = {
        entryType: 'questions',
        questions: Object.keys (qa),
        answers: Object.values (qa),
        start,
        end: new Date ()
      }
      await createEntry (entry);
      let all = Object.keys (qa).map (question => {
        return {
          key: question,
          value: '...'
        }
      });
      session.setAll (all);
      setStart (new Date ());
      unfreeze ();
    } catch (error) {
      console.log (error);
      unfreeze ();
    }
  }
  
  // call api to create audio entry
  const runSubmitAudioEntry = async e => {
    let unfreeze = freeze ();
    try {
      let body = {
        entryType: 'audio',
        start,
        end: new Date ()
      };
      let fileUploadResponse = await audioApi.post ({filename: audioRecordingTitle});
      let fileUpload = await fileUploadResponse.json ();
      await fetch (fileUpload.uploadUrl, {
        method: 'PUT',
        body: audioRecording
      });
      body.audio = fileUpload.id;
      body.title = audioRecordingTitle
      await createEntry (body);
      resetAudioInformation ();
      unfreeze ();
    } catch (e) {
      unfreeze ();
    }
  }

  // call api to create new question
  const runCreateQuestion = async () => {
    let unfreeze = null;
    try {
      let question = newQuestionRef.current.innerText;
      if (question.trim () === '' || question.trim === '?') return;
      unfreeze = freeze ();
      await createQuestion (question);
      newQuestionRef.current.innerText = '?';
      unfreeze ();
    } catch (e) {
      unfreeze ();
    }
  }
  useEffect (() => {
    set ('custom-question-editor')
    return () => set ('custom-question-editor');
  }, [page])

  if (page !== 'write' || !user) return null;
  if (getSetting ('freeform', '!audio-recording')) return (
    <main id="freeform-entry">
      <H1 short={'New Entry'}>New Freeform Entry</H1>
      <H2>{printDate ()}</H2>
      <H3>{getTime (start)}</H3>
      <div onFocus={onFirstFocus ('freeform')} ref={freeformRef} contentEditable>Your entry here!</div>
      <button onClick={runSubmitFreeformEntry}>Submit</button>
    </main>
  )
  if (getSetting ('freeform', '&audio-recording')) return (
    <main id="audio-entry">
    <H1 short={'New Entry'}>New Audio Entry</H1>
      <H2>{printDate ()}</H2>
      <H3>{getTime (start)}</H3>
      <AudioRecorder />
      {
        recordingStatus === 'finished' &&
        <button onClick={runSubmitAudioEntry}>Submit</button>
      }
      
    </main>
  )
  if (getSetting ('custom-question-editor')) return (
    <main id="custom-question-editor">
      <H1 short="Manage Questions">Manage Custom Questions</H1>
      <H2>Default Questions</H2>
      <ul className="current-question-list">
        {
          defaultQuestions.map (q => <li className="manage-default-question" key={`manage-default-questions-${q}`}>{q}</li>)
        }
      </ul>
      <H2>Custom Questions</H2>
      <ul className="current-question-list">
        {
          questions.map (q => (<CustomQuestionSetting question={q} onToggle={toggle} getSetting={getSetting} />))
        }
        </ul>
      <h2>Add A Custom Question?</h2>
      <h4>Add Question</h4>
      <div className="questions">
        <b>Add New Question</b>
        <div ref={newQuestionRef} onFocus={onFirstFocus ('create-question', '?')} className="add-question-input" contentEditable>...?</div>
      </div>
      <div>
        <button onClick={runCreateQuestion}>Add Question</button>
        <button onClick={toggle ('custom-question-editor')}>Back To Writing</button>
      </div>
    </main>
  )
  return (
    <main id="question-answer-entry">
      <H1 short={'New Entry'}>{'New Question & Answer Entry'}</H1>
      <H2>{printDate ()}</H2>
      <H3>{getTime (start)}</H3>
      <div className="questions">
          {
            getSetting ('default-questions') && defaultQuestions.map (question => (
              <>
                <b onClick={e => e.target.nextSibling.focus ()}>{question}</b>
                <div onFocus={onFirstFocus (question)} contentEditable onBlur={e => {
                  session.set (question, e.target.innerText);
                }}>{session.getSetting (question) || '...'}</div>
              </>
            ))
          }
          {
            getSetting ('custom-questions') && (
              <>
                {
                  questions.filter (q => getSetting (`show-custom-question-${q}`)).map (question => (
                    <>
                      <b>{question}</b>
                      <div onFocus={onFirstFocus (question)} contentEditable onBlur={e => {
                        session.set (question, e.target.innerText);
                      }}>{session.getSetting (question) || '...'}</div>
                    </>
                  ))
                }
                <span className="fake-button" onClick={toggle ('custom-question-editor')}>
                  Manage Custom Questions
                </span>
              </>
            )
          }
        </div>
        <button onClick={runSubmitQuestionAnswerEntry}>Submit</button>
    </main>
  )
}